.products {
    padding-bottom: 60px;

    @media screen and (min-width: 768px) {
        padding-bottom: 64px;
    }
}

.porducts__strong {
    display: block;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;

    text-align: center;
    text-transform: uppercase;

    margin-top: 60px;

    color: $text-strong;

    @media screen and (min-width: 768px) {
        font-size: 26px;
        line-height: 30px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 30px;
        line-height: 34px;
    }
}

.porducts__title {
    margin-top: 15px;
    margin-bottom: 8px;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $text-primary;

    @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 55px;
    }
    @media screen and (min-width: 1200px){
        font-size: 58px;
        line-height: 66px;
        margin-bottom: 20px;
    }

}

.porducts__list {
    @media screen and (min-width: 768px) {
        display: flex;
        margin:  158px -8px 0;
    }

    @media screen and (min-width: 1200px){
        
        margin: 265px -16px 0;
    }
}

.porducts__item {
    position: relative;
    margin-left: auto;
    margin-right: auto;

    width: 280px;
    margin-top: 140px;

    @media screen and (min-width: 768px) {
        width: calc((100% - 48px)/3);
        margin: 8px;
        
    }

    @media screen and (min-width: 1200px) {
        width: calc((100% - 96px)/3);
        margin: 16px;
    }
}

.porducts__item--cream::before {
    content: "";
    background-image: url(../image/mobile/mobile-ice-pink.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    margin-bottom: -50%;
    width: 280px;
    height: 280px;
    display: block;
    position: absolute;
        top: -140px;

        @media (min-device-pixel-ratio: 2),
            (-webkit-min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
                background-image:
                    url(../image/mobile/mobile-ice-pink2x.png);
            }

        @media screen and (min-width: 768px) {
            width: 224px;
            height: 224px;
            background-image: url(
                ../image/tablet/tablet-ice-pink.png
            );

            @media (min-device-pixel-ratio: 2),
                (-webkit-min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
                    background-image:
                        url(../image/tablet/tablet-ice-pink2x.png);
                }
            
        }
        
            @media screen and (min-width: 1200px) {
                width: 368px;
                height: 394px;
                top: -260px;
                background-image: url(../image/desctop/ice-pink.png);

                @media (min-device-pixel-ratio: 2),
                    (-webkit-min-device-pixel-ratio: 2),
                    (min-resolution: 192dpi),
                    (min-resolution: 2dppx) {
                        background-image:
                            url(../image/desctop/ice-pink2x.png);
                    }
            }
}

