.porducts-box {
    display: block;
    padding: 140px 0 40px 0;

    border-radius: 24px;

    &__cream{
        background: $header-bg;
    }
    &__cofe {
        background: $bg-cofe;
    }
    &__milkshakes {
        background: $color-olive;
    }
    @media screen and (min-width: 768px) {
        padding: 118px 0 40px 0;
    }
    @media screen and (min-width: 1200px){
        padding: 165px 0 60px 0;

    }
}

.porducts-box__title {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;

    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $text-white; 
    
    margin-top: 20px;
    margin-bottom: 0;

    @media screen and (min-width: 768px){
        margin-top: 0;
    }
    @media screen and (min-width: 1200px){
        font-size: 30px;
        margin-top: 0; 
    }
        
}

.porducts-box__title::after {
    content: "";
    background-image: url(../image/tri-tochki.svg);
    background-position: center;
    display: flex;
    margin: 32px auto;
    width: 26px;
    height: 6px;
}



.products-box__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.04em;

    margin-bottom: 32px;

    color: $text-white;

    margin: 0 auto;

    @media screen and (min-width: 768px) {
        line-height: 1.76;
        width: 160px;
    }
    @media screen and (min-width: 1200px){
        width: 245px;
        margin-bottom: 60px;
        font-size: 16px;
        line-height: 1.76;
    }
}

.products-button{
    display: flex;
    background: $text-white;
    margin: 32px auto 0;

    width: 40px;
    height: 40px;
    
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 50%;

    fill: $text-primary;
    
    .button__icon {
        transition: box-shadow $timing-function, width $timing-function, height $timing-function;
    }

    &:hover,
    &:focus {
        cursor: pointer;
        box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.2);
    }
    &:hover .button__icon,
    &:focus .button__icon{
        width: 15px;
        height: 20px;
    }
}


