.contacts__btn{
    width: 161.45px;
    height: 44px;
    font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: calc(21/16);
letter-spacing: 0.02em;
display: flex;
align-items: center;
text-align: center;
justify-content: center;
background-color: $text-primary;
border-radius: 22px;
border: 1px transparent;
padding: auto 17px;
margin-bottom: 10px;
color: $text-white;
&::after {
    content: "";
    background-image: url("../image/arrowRightWhite.svg");
    display: block;
    width: 8px;
    height: 12px;
    align-items: center;
    text-align: center;
    justify-content: center;
    background-size: auto;
    background-repeat: no-repeat;
    margin: auto 0 auto 10px;
}

&:hover,
&:focus{
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
@media screen and (min-width: 768px) {
    width: 191.45px;
    &:not(:last-child){
        margin-right: 14.5px;
    }
    }
@media screen and (min-width: 1200px) {
    width: 198px;
    &:not(:last-child){
        margin-right: 12px;
        }
    }
}

.contacts__button-light{
    background-color: $text-white;
    color: $text-primary;
    &::after {
        content: "";
        background-image: url("../image/arrowRight.svg");
        display: block;
        width: 8px;
        height: 12px;
        align-items: center;
        text-align: center;
        justify-content: center;
        background-size: auto;
        background-repeat: no-repeat;
        margin: auto 0 auto 10px;
        }
&:hover,
&:focus{
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
}