.advantages {
  padding-top: 40px;
  padding-bottom: 60px;

  background: $bg-about;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-top: 60px;
    padding-left: 32px;
    padding-bottom: 64px;
  }
  @media screen and (min-width: 1200px) {
    padding-top: 40px;
    padding-bottom: 115px;
  }
}

.advantages__list {
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    display: flex;
  }

  @media screen and (min-width: 1200px) {
    display: flex;
  }
}

.advantages__item {
  margin-bottom: 32px;

  &:last-child {
    margin-bottom: 0;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 0;
  }

  @media screen and (min-width: 1200px) {
    &:nth-child(2n) {
      width: 317px;
      margin-right: 80px;
      margin-left: 115px;
    }
  }
}

