.advantages__svg {
  width: 50px;
  height: 50px;

  background: $bg-about;

  margin-bottom: 16px;
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-bottom: 36px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 40px;
  }
}

.advantages__vector {
  width: 100%;
}

.advantages__subtitle--bucket::before {
  content: '';
  background-image: url(../image/bucket.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 50px;
  height: 50px;
  display: block;
  margin-bottom: 40px;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image:
        url(../image/bucket2x.png);
    }
}

.advantages__subtitle--apple::before {
  content: '';
  background-image: url(../image/apple.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 50px;
  height: 50px;
  display: block;
  margin-bottom: 40px;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image:
        url(../image/appple2x.png);
    }
}

.advantages__subtitle--heart::before {
  content: '';
    background-image: url(../image/heart.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 44px;
    height: 50px;
    display: block;
    margin-bottom: 40px;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image:
          url(../image/heart2x.png);
      }
}

.advantages__subtitle {
  display: block;
  padding-bottom: 15px;

  margin-bottom: 4px;

  font-family: 'Titan One';

  font-weight: 400;
  font-size: 36px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $text-primary;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    display: inline;

    font-size: 48px;
    line-height: 1.46;

    margin-bottom: 13px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.14;

    margin-bottom: 16px;
  }
}

.advantages__text {
  width: 230px;

  font-weight: 700;
  font-size: 12px;
  line-height: 1.7;

  color: $text-p-primary;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 180px;
    font-size: 14px;
    line-height: 1.74;
  }
  @media screen and (min-width: 1200px) {
    width: 288px;
    font-size: 16px;
    line-height: 1.76;

    letter-spacing: 0.02em;
  }
}
